.firstName_div {
    width: 100%;
    padding: 0px 40px;
    margin-top: 65px;
    height: 30px;
    margin-bottom: 20px;
  }
  .buttonDiv{
    width: 100%;
    padding: 0px 40px;
    margin-top: 70px;
    height: 25px;
    margin-bottom: 10px;
    height: 25px;
    display: flex;
    align-items: center;
    justify-content: center;

}
.loginbuttonDiv{
    width: 100%;
    padding: 0px 40px;
    margin-top: 30px;
    height: 25px;
    margin-bottom: 10px;
    height: 25px;
    display: flex;
    align-items: center;
    justify-content: center;
}
  .register_input_field {
    background: #f3f4f8;
  
    width: 100%;
  }
  .inputContainer{
    width: 500px;
    margin-top: 20px;
    border-radius: 8px;
    padding-bottom: 20px;
    margin-bottom: 50px;
    box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
  }
  .RegistermainContainer{
 display: flex;
 align-items: center;
 justify-content: center;
  }
  .headingContainer{
    display: flex;
    align-items: center;
    gap: 20px;
    justify-content: center;
  }
  .headingContainer span{
    font-size: 30px;
    font-weight:bold;

  }
  .selectedSpan{
    cursor:pointer;
    font-size: 30px;
    font-weight:bold;
    color:#6364BF;
  }
  .forgotText{
    width: auto;
    padding: 0px 40px;
    margin-top: 40px;
    cursor: pointer;

  }
  .forgotTextContainer{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content:center;
  }